export default {
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      form: {
        imgUrl: "",
        details: "",
        price: undefined,
        remark: ""
      },
      imgView: false,
      outerVisible: false,
      innerVisible: false,
      isForm: true,
      loading: false,
      dialogVisible: false,
      imgList: [],
      detailsList: [],
      priceList: [],
      remarkList: []
    };
  },
  computed: {
    openCardDialog: {
      get: function get() {
        return this.visible;
      },
      set: function set(val) {
        this.$emit("update:visible", val); // openCardDialog改变的时候通知父组件
      }
    }
  },
  mounted: function mounted() {},
  methods: {
    openForm: function openForm() {
      var _this = this;

      this.isForm = true;
      this.loading = true;
      setTimeout(function () {
        _this.loading = false;
      }, 800);
    },
    uploadOpen: function uploadOpen() {
      document.getElementById("imgFile").click();
    },
    Base64: function Base64(el) {
      var _this2 = this;

      if (el.target.value) {
        var reader = new FileReader(),
            img = new Image(),
            ext = "";

        reader.onload = function (e) {
          img.src = e.target.result;
          var extArr = e.target.result.match(/data:image\/(.*);base64,.*/);
          ext = extArr[1];
        };

        img.onload = function (e) {
          base64Image({
            file: el.target,
            callback: function callback(rst) {
              var base64 = rst.split("base64,").pop();

              _this2.uploadImgBase64(base64, ext);
            }
          });
        };

        reader.readAsDataURL(el.target.files[0]);
      }
    },
    uploadImgBase64: function uploadImgBase64(base64, ext) {
      var _this3 = this;

      this.loading = true;
      this.$apiCall("api.User.uploadImgBase64", {
        imgUrlBase64: base64,
        ext: ext
      }, function (r) {
        _this3.loading = false;

        if (r.ErrorCode == 9999) {
          _this3.form.imgUrl = r.Data.Results.imgUrl;
        } else {
          _this3.$message.error(r.Message);
        }
      });
    },
    saveQuotation: function saveQuotation() {
      if (this.form.imgUrl == "") {
        this.$message.error("Please select a picture !");
        return false;
      }

      if (this.form.details == "") {
        this.$message.error("Please fill in the details !");
        return false;
      }

      this.imgView = true;
      this.isForm = false;
      this.imgList.push(this.form.imgUrl);
      this.detailsList.push(this.form.details);
      this.priceList.push(this.form.price);
      this.remarkList.push(this.form.remark);
      this.form = {
        imgUrl: "",
        details: "",
        price: undefined,
        remark: ""
      };
    },
    closeQuotation: function closeQuotation() {
      this.openCardDialog = false;
      this.form = {
        imgUrl: "",
        details: "",
        price: undefined,
        remark: ""
      };
      this.imgList = [];
      this.detailsList = [];
      this.priceList = [];
      this.remarkList = [];
      this.isForm = true;
      this.imgView = false;
    },
    closeDialog: function closeDialog() {
      this.closeQuotation();
    },
    submit: function submit() {
      var _this4 = this;

      var products = [];

      for (var i = 0; i < this.imgList.length; i++) {
        products.push({
          detail: this.detailsList[i],
          imgUrl: this.imgList[i],
          expectPrice: this.priceList[i],
          remarks: this.remarkList[i]
        });
      }

      this.$apiCall("api.OfferList.genOfferProduct", {
        products: products
      }, function (r) {
        if (r.ErrorCode == 9999) {
          _this4.$message.success("Submit Success !");

          _this4.closeQuotation();

          _this4.$router.push('/Waiting-for-vendors-respond');
        } else {
          _this4.$message.error(r.Message);
        }
      });
    }
  },
  components: {}
};