import Quotation from "../../components/productQuotation";
export default {
  data: function data() {
    return {
      visible: false,
      form: {
        email: ''
      },
      tdShow: false,
      canChange: true,
      verifyEmail: false,
      changeStart: false,
      sendMsgDisabled: false,
      canSaveEdit: false,
      canSendAuthCode: false,
      canSendAuthCodeAgain: false,
      time: 0,
      userEmail: "",
      authcode: "",
      failTx: "",
      pageKey: new Date().valueOf(),
      pendingPay: 0,
      activeName: "",
      reSizeTime: 0,
      allocateCnt: 0,
      sourcingCnt: 0,
      processingCnt: 0,
      abnormalCnt: 0,
      fulfillArr: {
        fulfilled: {
          text: "Fulfilled",
          type: "info",
          r: 3
        },
        unfulfilled: {
          text: "Unfulfilled",
          type: "warning",
          r: 1
        },
        partial: {
          text: "Partially Fulfilled",
          type: "danger",
          r: 2
        },
        refunded: {
          text: "Refunded",
          type: "info",
          r: 3
        }
      },
      dateArr: {
        1: 'Today',
        2: 'Last 7 days',
        3: 'Last 30 days',
        4: 'Last 90 days',
        5: 'Last 12 months',
        6: 'Custom'
      },
      updateOrderLoading: false,
      cnow: 0,
      now: Date.parse(new Date()) / 1000 - this.$store.state.userInfo.timeDiff,
      updateTime: localStorage.getItem('c_orders-updateTime') ? Number(localStorage.getItem('c_orders-updateTime')) : 0,
      exportDialog: {
        isShow: false,
        loading: false,
        status: "",
        selectData: [],
        totalCount: 0
      },
      exportDialogDefault: '{}',
      dialogHowToShopify: {
        isShow: false
      }
    };
  },
  components: {
    Quotation: Quotation
  },
  mounted: function mounted() {
    var _this = this;

    this.showPopup();
    this.exportDialogDefault = JSON.stringify(this.exportDialog);
    this.activeName = this.$route.name;
    this.cnow = setInterval(function () {
      _this.now = Date.parse(new Date()) / 1000 - _this.$store.state.userInfo.timeDiff;
    }, 1000);
    this.getNum(undefined, 'first');
  },
  watch: {
    $route: function $route() {
      this.activeName = this.$route.name;
      this.getNum();
    }
  },
  methods: {
    handleCommand: function handleCommand(c) {
      switch (c) {
        case 'quoteForProduct':
          this.quotation();
          break;

        case 'recentQuotations':
          this.openRecent();
          break;
      }
    },
    makeAnOffer: function makeAnOffer() {
      this.$refs.subPage.makeAnOffer();
    },
    quotation: function quotation() {
      this.$Burying({
        object: '14001'
      });
      this.visible = true;
    },
    openRecent: function openRecent() {
      this.$router.push('/make-offers');
    },
    // 利用cookie限制每天只弹窗一次
    // expires指定时间删除有效期24小时的cookie
    showPopup: function showPopup() {
      //====================
      var s = document.cookie;

      if (s.indexOf('tdShow=1') != -1) {
        this.$nextTick(function () {
          this.tdShow = false;
        });
        return;
      }

      var d = new Date();
      d.setHours(d.getHours() + 24);
      document.cookie = 'tdShow=1;expires=' + d.toGMTString(); // 设置cookie
      // ====================
      // 以下是要执行的代码

      this.$nextTick(function () {
        if (JSON.parse(localStorage.getItem("c_ourMallUserInfo")).verifyEmail == "2") {
          this.verifyEmailOpen();
        }

        ;
        this.tdShow = true;
      });
    },
    saveEmail: function saveEmail() {
      var _this2 = this;

      this.$apiCall("api.User.checkEmailToken", {
        email: this.changeStart == true ? this.userEmail : this.form.email,
        token: this.authcode
      }, function (r) {
        if (r.ErrorCode == '9999') {
          var userInfo = JSON.parse(JSON.stringify(_this2.$store.state.userInfo));
          userInfo.email = _this2.changeStart == true ? _this2.userEmail : _this2.form.email;
          localStorage.setItem("c_ourMallUserInfo", JSON.stringify(userInfo));

          _this2.$elementMessage('User Email modified successfully', "success");

          _this2.verifyEmail = false;
        } else {
          _this2.$elementMessage(r.Message, "error");
        }
      });
    },
    changeOpen: function changeOpen() {
      this.changeStart = true;
      this.form.email = "";
    },
    // 验证邮箱格式
    isCEmail: function isCEmail(email) {
      return email ? /^([a-z0-9_\.-]+)@([\da-z\.-]+)\.([a-z\.]{2,6})$/.test(email) : false;
    },
    successVerify: function successVerify() {
      if (this.userEmail && this.isCEmail(this.userEmail) || this.form.email && this.isCEmail(this.form.email)) {
        this.canSendAuthCode = true;
        this.econfirmSuccess = true;
        this.canChange = false;
        this.failTx = "";
        this.sendAuthCode();
      } else {
        this.$refs.verify.reset();
        this.canSendAuthCode = false;
        this.econfirmSuccess = false;
        this.failTx = "Please fill in the correct Email address";
      }
    },
    // 发送验证码
    sendAuthCode: function sendAuthCode() {
      var _this3 = this;

      this.$showLoading();
      var vm = this;
      vm.sendMsgDisabled = true;
      vm.canSendAuthCode = false;
      vm.canSendAuthCodeAgain = true; // 邮箱验证码

      this.$apiCall("api.User.checkEmail", {
        email: vm.changeStart == true ? vm.userEmail : vm.form.email
      }, function (r) {
        _this3.$hideLoading();

        if (r.ErrorCode == '9999') {
          _this3.$message({
            type: 'success',
            message: 'The captcha was sent successfully'
          });

          vm.time = 60;
          var interval = window.setInterval(function () {
            if (vm.time-- <= 0) {
              vm.time = 0;
              vm.sendMsgDisabled = false;
              vm.canSendAuthCode = true;
              vm.canSendAuthCodeAgain = false;
              window.clearInterval(interval); //停止
            }
          }, 1000);
        } else {
          _this3.$message({
            message: r.Message,
            type: "error"
          });

          vm.sendMsgDisabled = false;
          vm.canSendAuthCode = true;
          vm.canSendAuthCodeAgain = false;
        }
      });
    },
    verifyEmailOpen: function verifyEmailOpen() {
      var email = JSON.parse(localStorage.getItem("c_ourMallUserInfo")).email;
      this.form.email = email;
      this.verifyEmail = true;
    },
    goto: function goto(name) {
      this.$router.push({
        name: name
      });
      this.dialogHowToShopify.isShow = false;
    },
    handlePendingPay: function handlePendingPay() {
      if (this.$route.name === "OrderProcessing") {
        this.$refs.subPage.filterParams.status = 2;
        this.$refs.subPage.getItem();
      }

      this.$router.push({
        path: "/Orders-processing",
        query: {
          status: 2
        }
      });
    },
    exportOrders: function exportOrders() {
      var _this4 = this;

      //导出订单
      var params = {
        email: this.$store.state.userInfo.email
      };

      if (this.activeName == 'AllOrders') {
        //all orders
        params.isAll = 1;
        var _filter = this.$refs.subPage.filterParams;

        if (this.exportDialog.status == 3) {
          params.accountIds = _filter.shopifyStore;
          params.timeFilter = _filter.timeFilter >= 6 ? "" : _filter.timeFilter;
          params.dateFrom = _filter.dateFrom;
          params.dateTo = _filter.dateTo;
          params.fulfillmentStatus = _filter.fulfillStatus;
          params.sort = _filter.sortBy;
          params.keywords = _filter.codeName;
          params.financialStatus = _filter.financial_status;
        }
      }

      if (this.activeName == 'WaitingForAllocated') {
        //waiting for allocated
        params.waitDispatch = 1;
        var _filter2 = this.$refs.subPage.filterParams;

        if (this.exportDialog.status == 3) {
          params.accountIds = _filter2.shopifyStore;
          params.timeFilter = _filter2.timeFilter >= 6 ? "" : _filter2.timeFilter;
          params.dateFrom = _filter2.dateFrom;
          params.dateTo = _filter2.dateTo;
          params.fulfillmentStatus = _filter2.fulfillStatus;
          params.sort = _filter2.sortBy;
          params.keywords = _filter2.codeName;
        }
      }

      this.exportDialog.loading = true;
      this.$apiCall("api.Invoice.exportShopifyOrders", params, function (r) {
        _this4.exportDialog.loading = false;

        if (r.ErrorCode == 9999) {
          _this4.exportDialog.isShow = false;

          _this4.$message({
            offset: 200,
            duration: 0,
            dangerouslyUseHTMLString: true,
            showClose: true,
            message: "<strong>Your Orders are exporting</strong><br/>Your export will be delivered by email to: ".concat(_this4.$store.state.userInfo.email, ". <br/>Depending on how many Orders you\u2019re exporting, this could take some time."),
            type: 'success'
          });
        } else {
          _this4.$elementMessage(r.Message, 'error');
        }
      });
    },
    openExportDialog: function openExportDialog() {
      this.exportDialog = JSON.parse(this.exportDialogDefault);
      this.exportDialog.isShow = true;
      this.exportDialog.loading = false;

      if (this.activeName == 'AllOrders' || this.activeName == 'WaitingForAllocated') {
        this.exportDialog.totalCount = this.$refs.subPage.totalCount;
      }
    },
    updateOrder: function updateOrder() {
      var _this5 = this;

      this.updateOrderLoading = true;
      this.$apiCall('api.ShopifyAccount.getOrderByHand', {}, function (r) {
        _this5.updateOrderLoading = false;

        if (r.ErrorCode == "9999") {
          _this5.updateTime = r.Data.Results;
          localStorage.setItem('c_orders-updateTime', r.Data.Results);

          _this5.$elementMessage('Update Order Success', 'success');
        } else {
          _this5.$elementMessage(r.Message, 'error');
        }
      });
    },
    getNum: function getNum(time, type) {
      var _this6 = this;

      this.$apiCall("api.Invoice.getOrderCnt", {}, function (r) {
        if (r.ErrorCode == "9999") {
          var data = r.Data.Results;
          var t = setInterval(function () {
            if (!time || new Date().valueOf() > time + 1500) {
              clearInterval(t);
              _this6.allocateCnt = Number(data.allocateCnt);
              _this6.sourcingCnt = Number(data.sourcingCnt);
            }
          }, 100);
          _this6.processingCnt = Number(data.processingCnt);
          _this6.abnormalCnt = Number(data.abnormalCnt);

          if (type == 'first') {
            setTimeout(function () {
              if (_this6.allocateCnt && !Number(_this6.$store.state.userInfo.vendorCnt)) {
                _this6.dialogHowToShopify.isShow = true;
              }
            }, 200);
          }
        } else {
          _this6.$elementMessage(r.Message, "error");
        }
      });
      this.$apiCall("api.Invoice.findByCustomer", {
        status: 2
      }, function (r) {
        if (r.ErrorCode == "9999") {
          var data = r.Data.Results;
          _this6.pendingPay = data.length;
        } else {
          _this6.$elementMessage(r.Message, "error");
        }
      });
    },
    getTableHeight: function getTableHeight(children) {
      var _this7 = this;

      //获取表格高度
      clearTimeout(this.reSizeTime);
      this.reSizeTime = setTimeout(function () {
        var h = _this7.$root.$children[0].$refs.mainScroll.wrap.offsetHeight - (_this7.$refs.pageHeader.offsetHeight + _this7.$refs.pageTabs.$el.offsetHeight + children.$refs.tablefilter.offsetHeight + children.$refs.tableFooter.offsetHeight) - 120;
        if (_this7.activeName == 'WaitingForAllocated') h += -10;
        children.tableHeight = h > 200 ? h : 200;
      }, 500);
    },
    handleClick: function handleClick(tab) {
      if (tab.name == this.$route.name) {
        //点击相同路由刷新
        this.pageKey = new Date().valueOf();
      } else {
        //tabs 切换
        this.$router.push({
          name: tab.name
        });
      }
    }
  }
};